<template>
    <div class="success-popup popup" v-if="showPopup">
        <svg width="24" fill="green" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd">
            <path
                d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm7 7.457l-9.005 9.565-4.995-5.865.761-.649 4.271 5.016 8.24-8.752.728.685z" />
        </svg>
        <div class="popup-text">
            The article was edited
        </div>
        <div class="close" @click="closePopUp">
            <svg clip-rule="evenodd" width="16px" height="16px" fill-rule="evenodd" stroke-linejoin="round"
                stroke-miterlimit="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                <path
                    d="m12 10.93 5.719-5.72c.146-.146.339-.219.531-.219.404 0 .75.324.75.749 0 .193-.073.385-.219.532l-5.72 5.719 5.719 5.719c.147.147.22.339.22.531 0 .427-.349.75-.75.75-.192 0-.385-.073-.531-.219l-5.719-5.719-5.719 5.719c-.146.146-.339.219-.531.219-.401 0-.75-.323-.75-.75 0-.192.073-.384.22-.531l5.719-5.719-5.72-5.719c-.146-.147-.219-.339-.219-.532 0-.425.346-.749.75-.749.192 0 .385.073.531.219z" />
            </svg>
        </div>
    </div>
    <div class="container">
        <div class="title">
            Edit article
        </div>
        <form class="addForm">
            <div class="field">
                <label for="apTitle">Date (Month, DD, YYYY)</label>
                <input type="text" v-model="dataObj.date" v-if="currentData" required name="apTitle" class="form-input">
            </div>
            <div class="field">
                <label for="apTitle">Article preview title (on Blog page)</label>
                <input type="text" required name="apTitle" v-model="dataObj.apPreviewTitle" v-if="currentData"
                    class="form-input">
            </div>
            <div class="field">
                <label for="apTitle">Article preview subtitle</label>
                <input type="text" required name="apTitle" v-model="dataObj.apPreviewDescription" v-if="currentData"
                    class="form-input">
            </div>
            <div class="field">
                <label for="apTitle">Tags (put the tags in commas)</label>
                <input type="text" required name="apTitle" @change="cutTags" v-model="dataObj.tags" v-if="currentData"
                    class="form-input">
            </div>
            <div class="field">
                <label for="apTitle">Author name:</label>
                <input type="text" required name="apTitle" v-model="dataObj.author" v-if="currentData" class="form-input">

            </div>
        </form>
        <div class="ap-img">
            <div class="upload-block">
                <span>Preview image (387 x 134px only .png)</span>
                <label for="file-upload" id="prevImg" class="custom-file-upload">
                    Upload
                </label>
                <input id="file-upload" type="file" @change="uploadFilePreview" required accept=".png,.jpg,.jpeg" />
            </div>

            <div class="img-preview">
                <span>Current file: {{ previewFilename }}</span>

            </div>
        </div>
        <div class="ap-img">
            <div class="upload-block">
                <span>Main article image (1309px x 307px only .png)</span>
                <label for="main-img" class="custom-file-upload">
                    Upload
                </label>
                <input id="main-img" type="file" @change="uploadMainFile" accept=".png,.jpg,.jpeg" required />
            </div>
            <div class="img-preview">
                <span>Current file: {{ mainImgFilename }}</span>

            </div>
        </div>
        <div class="ap-img">
            <div class="upload-block">
                <span>Author Photo</span>
                <label for="author-img" class="custom-file-upload">
                    Upload
                </label>
                <input id="author-img" type="file" @change="uploadAuthorFile" accept=".png,.jpg,.jpeg" required />
            </div>
            <div class="img-preview">
                <span>Current file: {{ authorPhotoFilename }}</span>

            </div>
        </div>

        <div class="article-editor" id="editor">
            <span>Article content (insert elements as they should be in the article)</span>
            <!-- <QuillEditor v-if="currentData" @ready="updateContent()" ref="editor" toolbar="full" /> -->
            <MdEditor v-if="currentData" v-model="dataObj.markdownContent" :language="'en-US'" @onChange="updateHtmlContent"
                :change="updateHtmlContent" @onUploadImg="onUploadImg($event, true)" />


        </div>
        <button class="lead-btn" @click="updateData">Send</button>



    </div>
</template>
<script>
import { defineComponent } from 'vue';
import getObjectFromRoot from '../getData';
import { MdEditor } from 'md-editor-v3';
import 'md-editor-v3/lib/style.css';
import { uploadBytes, uuidv4, ref, db, storage, getDownloadURL, sRef } from '../firebaseConfig'
import MarkdownIt from 'markdown-it';
import { set } from "firebase/database";


export default defineComponent({
    components: {
        MdEditor,
    },

    data() {
        return {
            rootData: [], //    rootData 
            currentData: null, //   null,       
            htmlContent: '',
            previewFilename: '',
            authorPhotoFilename: '',
            mainImgFilename: '',
            lastIndex: '',
            showPopup: false,
            dataObj: {
                date: '',
                apPreviewTitle: '',
                apPreviewDescription: '',
                tags: null,
                convertedTags: '',
                previewImageUrl: '',
                mainArticleImageUrl: '',
                content: '',
                markdownTags: '',
                articleId: '',
                author: '',
                authorPhoto: '',
                markdownContent: '',
            }

        };
    },
    async created() {
        this.rootData = await getObjectFromRoot();
        const index = this.$route.query.id;
        if (index !== null) {
            this.currentData = this.rootData[index];
            console.log(this.currentData.markdownTags)
            this.dataObj.markdownContent = this.currentData.markdownTags
            console.log(this.currentData.markdownTags)
        } else {
            console.error("  URL    .");
        }

        if (this.currentData) {
            this.dataObj.date = this.currentData.date; //      
            this.dataObj.apPreviewTitle = this.currentData.apPreviewTitle; //      
            this.dataObj.apPreviewDescription = this.currentData.apPreviewDescription; //      
            this.dataObj.tags = this.currentData.tags; //      
            this.dataObj.previewImageUrl = this.currentData.previewImageUrl;
            this.dataObj.mainArticleImageUrl = this.currentData.mainArticleImageUrl;
            this.dataObj.author = this.currentData.author;
            this.dataObj.authorPhoto = this.currentData.authorPhoto
        }
        this.dataObj.tags = JSON.stringify(this.dataObj.tags); // eslint-disable-next-line 
this.dataObj.tags = (this.dataObj.tags.replace(/[\[\]"\\"']/g, ""));

    },
    methods: {
        async uploadMainFile(event) {
            this.file = event.target.files[0];
            if (this.file) {
                const randomName = uuidv4();
                const storageRef = ref(storage, `articlesImg/${randomName}${this.file.name}`);
                try {
                    await uploadBytes(storageRef, this.file);
                    console.log("File uploaded successfully!");
                    const url = await getDownloadURL(storageRef);
                    this.dataObj.mainArticleImageUrl = url
                    this.mainImgFilename = this.file.name
                } catch (error) {
                    console.error("Error uploading file:", error);
                }
            }
        },
        async uploadFilePreview(event) {
            this.file = event.target.files[0];
            if (this.file) {
                const randomName = uuidv4();
                const storageRef = ref(storage, `articlesImg/${randomName}${this.file.name}`);
                try {
                    await uploadBytes(storageRef, this.file);
                    console.log("File uploaded successfully!");
                    const url = await getDownloadURL(storageRef);
                    this.dataObj.previewImageUrl = url
                    this.previewFilename = this.file.name
                    console.log(this.dataObj)
                } catch (error) {
                    console.error("Error uploading file:", error);
                }
            }
        },
        async uploadAuthorFile(event) {
            this.file = event.target.files[0];
            if (this.file) {
                const randomName = uuidv4();
                const storageRef = ref(storage, `articlesImg/${randomName}${this.file.name}`);
                try {
                    await uploadBytes(storageRef, this.file);
                    console.log("File uploaded successfully!");
                    const url = await getDownloadURL(storageRef);
                    this.dataObj.authorPhoto = url
                    this.authorPhotoFilename = this.file.name
                } catch (error) {
                    console.error("Error uploading file:", error);
                }
            }
        },
        updateHtmlContent() {
            //  markdown  HTML
            const md = new MarkdownIt();
            this.htmlContent = md.render(this.dataObj.markdownContent);
            console.log(this.htmlContent); //  HTML-  
            this.dataObj.content = this.htmlContent
            this.dataObj.markdownTags = this.dataObj.markdownContent
        },
        onUploadImg(files, fromMdEditor) {
            if (fromMdEditor) {
                const file = files[0];
                const randomName = uuidv4();
                const fileRef = ref(storage, `articlesImg/${randomName}`);

                uploadBytes(fileRef, file).then(async (snapshot) => {
                    console.log('Uploaded a blob or file!', snapshot);

                    //  URL  
                    const imageUrl = await getDownloadURL(fileRef);

                    //   img   URL
                    const imgTag = `<img src="${imageUrl}" alt="Uploaded Image" />`;

                    //   img  markdownContent
                    this.markdownContent += imgTag;
                    this.updateHtmlContent();
                }).catch((error) => {
                    console.error('Error uploading file:', error);
                });
            } else {
                //   ,     MdEditor
                //         
            }
        },
        cutTags() {
            let cutted = this.dataObj.tags.split(',');
            this.dataObj.convertedTags = Object.assign({}, cutted);
        },
        async updateData() {

            let cutted = this.dataObj.tags.split(',');
            this.dataObj.convertedTags = Object.assign({}, cutted);

            const md = new MarkdownIt();
            this.htmlContent = md.render(this.dataObj.markdownContent);
            console.log(this.htmlContent); //  HTML-  
            this.dataObj.content = this.htmlContent
            this.dataObj.markdownTags = this.dataObj.markdownContent


            console.log('Method is called');
            const article = this.$route.query.id;
            this.dataObj.articleId = article
            console.log(article);
            if (!article) {
                console.error("Article ID not found in URL.");
                return;
            }



            try {
                set(sRef(db, `/${article}`), {
                    date: this.dataObj.date,
                    apPreviewTitle: this.dataObj.apPreviewTitle,
                    apPreviewDescription: this.dataObj.apPreviewDescription,
                    tags: this.dataObj.convertedTags,
                    previewImageUrl: this.dataObj.previewImageUrl,
                    mainArticleImageUrl: this.dataObj.mainArticleImageUrl,
                    content: this.dataObj.content,
                    markdownTags: this.dataObj.markdownTags,
                    articleId: this.dataObj.articleId,
                    author: this.dataObj.author,
                    authorPhoto: this.dataObj.authorPhoto
                })

                this.showPopup = true
                console.log('Updated')
                setTimeout(() => {
                    this.showPopup = false
                }, 3000);

            } catch (err) {
                console.log(`Error while sending data to server: ${err}`)
            }

        },
        closePopUp() {
            this.showPopup = false;
        }


    },

});


</script>
