            <svg clip-rule="evenodd" width="16px" height="16px" fill-rule="evenodd" stroke-linejoin="round"
                stroke-miterlimit="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                <path
                    d="m12 10.93 5.719-5.72c.146-.146.339-.219.531-.219.404 0 .75.324.75.749 0 .193-.073.385-.219.532l-5.72 5.719 5.719 5.719c.147.147.22.339.22.531 0 .427-.349.75-.75.75-.192 0-.385-.073-.531-.219l-5.719-5.719-5.719 5.719c-.146.146-.339.219-.531.219-.401 0-.75-.323-.75-.75 0-.192.073-.384.22-.531l5.719-5.719-5.72-5.719c-.146-.147-.219-.339-.219-.532 0-.425.346-.749.75-.749.192 0 .385.073.531.219z" />
            </svg>
        </div>
    </div>
    <div class="container">
        <div class="title">
            Add new blog article
        </div>
        <form class="addForm">
            <div class="field">
                <label for="apTitle">Date (Month, DD, YYYY)</label>
                <input type="text" v-model="dataArr.date" required name="apTitle" class="form-input">
            </div>
            <div class="field">
                <label for="apTitle">Article preview title (on Blog page)</label>
                <input type="text" required name="apTitle" v-model="dataArr.apPreviewTitle" class="form-input">
            </div>
            <div class="field">
                <label for="apTitle">Author name:</label>
                <input type="text" required name="apTitle" v-model="dataArr.author" class="form-input">

            </div>
            <div class="field">
                <label for="apTitle">Article preview subtitle</label>
                <input type="text" required name="apTitle" v-model="dataArr.apPreviewDescription" class="form-input">
            </div>
            <div class="field">
                <label for="apTitle">Tags (put the tags in commas)</label>
                <input type="text" required name="apTitle" @change="cutTags" v-model="dataArr.tags" class="form-input">
            </div>
        </form>
        <div class="ap-img">
            <div class="upload-block">
                <span>Preview image (387 x 134px only .png)</span>
                <label for="file-upload" id="prevImg" class="custom-file-upload">
                    Upload
                </label>
                <input id="file-upload" type="file" @change="uploadFilePreview" required accept=".png,.jpg,.jpeg" />
            </div>

            <div class="img-preview">
                <span>Current file: {{ previewFilename }}</span>

            </div>
        </div>
        <div class="ap-img">
            <div class="upload-block">
                <span>Main article image (1309px x 307px only .png)</span>
                <label for="main-img" class="custom-file-upload">
                    Upload
                </label>
                <input id="main-img" type="file" @change="uploadMainFile" accept=".png,.jpg,.jpeg" required />
            </div>
            <div class="img-preview">
                <span>Current file: {{ mainImgFilename }}</span>

            </div>
        </div>
        <div class="ap-img">
            <div class="upload-block">
                <span>Author Photo</span>
                <label for="author-img" class="custom-file-upload">
                    Upload
                </label>
                <input id="author-img" type="file" @change="uploadAuthorFile" accept=".png,.jpg,.jpeg" required />
            </div>
            <div class="img-preview">
                <span>Current file: {{ authorPhotoFilename }}</span>

            </div>
        </div>

        <div class="article-editor" id="editor">
            <span>Article content (insert elements as they should be in the article)</span>
            <!-- <QuillEditor v-if="currentData" @ready="updateContent()" ref="editor" toolbar="full" /> -->
            <MdEditor v-model="markdownContent" :language="'en-US'" @onChange="updateHtmlContent"
                :change="updateHtmlContent" @onUploadImg="onUploadImg($event, true)" />


        </div>
        <button class="lead-btn" @click="sendDataToServer">Send</button>



    </div>
</template>
<script>
import { defineComponent } from 'vue';
import { MdEditor } from 'md-editor-v3';
import 'md-editor-v3/lib/style.css';
import MarkdownIt from 'markdown-it';
import { uuidv4, ref, storage, getDownloadURL, uploadBytes } from '@/firebaseConfig';
import { db, sRef, get } from '@/firebaseConfig';
import { set } from 'firebase/database';
// import { set } from "firebase/database";


export default defineComponent({
    components: {
        MdEditor,
    },
    data() {
        return {
            markdownContent: '',
            htmlContent: '',
            previewFilename: '',
            mainImgFilename: '',
            authorPhotoFilename: '',
            lastIndex: '',
            showPopup: false,
            index: '',
            dataArr: {
                date: '',
                apPreviewTitle: '',
                apPreviewDescription: '',
                tags: '',
                convertedTags: '',
                previewImageUrl: '',
                mainArticleImageUrl: '',
                content: '',
                markdownTags: '',
                articleId: null,
                authorPhoto: '',
                author: '',

            }
        }
    },
    methods: {

        onUploadImg(files, fromMdEditor) {
            if (fromMdEditor) {
                const file = files[0];
                const randomName = uuidv4();
                const fileRef = ref(storage, `articlesImg/${randomName}`);

                uploadBytes(fileRef, file).then(async (snapshot) => {
                    console.log('Uploaded a blob or file!', snapshot);

                    //  URL  
                    const imageUrl = await getDownloadURL(fileRef);

                    //   img   URL
                    const imgTag = `<img src="${imageUrl}" alt="Uploaded Image" />`;

                    //   img  markdownContent
                    this.markdownContent += imgTag;
                    this.updateHtmlContent();
                }).catch((error) => {
                    console.error('Error uploading file:', error);
                });
            } else {
                //   ,     MdEditor
                //         
            }
        },
        async uploadMainFile(event) {
            this.file = event.target.files[0];
            if (this.file) {
                const randomName = uuidv4();
                const storageRef = ref(storage, `articlesImg/${randomName}${this.file.name}`);
                try {
                    await uploadBytes(storageRef, this.file);
                    console.log("File uploaded successfully!");
                    const url = await getDownloadURL(storageRef);
                    this.dataArr.mainArticleImageUrl = url
                    this.mainImgFilename = this.file.name
                } catch (error) {
                    console.error("Error uploading file:", error);
                }
            }
        },
        async uploadAuthorFile(event) {
            this.file = event.target.files[0];
            if (this.file) {
                const randomName = uuidv4();
                const storageRef = ref(storage, `articlesImg/${randomName}${this.file.name}`);
                try {
                    await uploadBytes(storageRef, this.file);
                    console.log("File uploaded successfully!");
                    const url = await getDownloadURL(storageRef);
                    this.dataArr.authorPhoto = url
                    this.authorPhotoFilename = this.file.name
                } catch (error) {
                    console.error("Error uploading file:", error);
                }
            }
        },
        async uploadFilePreview(event) {
            this.file = event.target.files[0];
            if (this.file) {
                const randomName = uuidv4();
                const storageRef = ref(storage, `articlesImg/${randomName}${this.file.name}`);
                try {
                    await uploadBytes(storageRef, this.file);
                    console.log("File uploaded successfully!");
                    const url = await getDownloadURL(storageRef);
                    this.dataArr.previewImageUrl = url
                    this.previewFilename = this.file.name
                    console.log(this.dataArr)
                } catch (error) {
                    console.error("Error uploading file:", error);
                }
            }
        },
        updateHtmlContent() {
            //  markdown  HTML
            const md = new MarkdownIt();
            this.htmlContent = md.render(this.markdownContent);
            console.log(this.htmlContent); //  HTML-  
            this.dataArr.content = this.htmlContent
            this.dataArr.markdownTags = this.markdownContent
        },
        async uploadPreview() {
            if (this.file.length > 0) { // ,     this.file
                const file = file[0];
                const randomName = uuidv4();
                const filePath = ref(storage, `articlesImg/${randomName}`);

                try {
                    await uploadBytes(filePath, file);
                    console.log('Uploaded a blob or file!');

                    const fileUrl = await getDownloadURL(filePath);
                    console.log(fileUrl);
                } catch (error) {
                    console.error('Error uploading file:', error);
                }
            } else {
                console.warn('No file selected.');
            }
        },
        cutTags() {
            let cutted = this.dataArr.tags.split(','),
                tagsObj = Object.assign({}, cutted);
            console.log(this.dataArr.convertedTags = tagsObj)
            return this.dataArr.convertedTags = tagsObj;
        },
        async check () {
            const rootRef = sRef(db, "/"); //       
            const snapshot = await get(rootRef);

            //    
            const rootData = snapshot.val();
            console.log(rootData)
            if (rootData) {
                console.log(Object.keys(rootData))
            const count = Object.keys(rootData)
            console.log(count)
            } else {
                console.log('Articles not found :(')
            }
           
        },
        async sendDataToServer() {
            const rootRef = sRef(db, "/"); //       
            const snapshot = await get(rootRef);

            //    
            const rootData = snapshot.val();
            if (rootData) {
                console.log(Object.keys(rootData))
            const count = Object.keys(rootData)
            this.lastIndex = count.length;
            this.dataArr.articleId = count.length;
            console.log(this.lastIndex)

            } else {
                console.log('Articles not found :(')
            }
           
            let hasError = false;

            if (this.dataArr.date === '') {
                hasError = true
            }

            if (this.dataArr.apPreviewTitle === '') {
                hasError = true
            }

            if (this.dataArr.apPreviewDescription === '') {
                hasError = true
            }

            if (this.dataArr.author === '') {
                hasError = true
            }

            if (this.dataArr.convertedTags === '') {
                hasError = true
            }

            if (this.dataArr.previewImageUrl === '') {
                hasError = true
            }

            if (this.dataArr.mainArticleImageUrl === '') {
                hasError = true
            }

            if (this.dataArr.markdownTags === '') {
                hasError = true
            }

            if (!hasError) {
                try {
                    set(sRef(db, `/${this.lastIndex}`), {
                        date: this.dataArr.date,
                        apPreviewTitle: this.dataArr.apPreviewTitle,
                        apPreviewDescription: this.dataArr.apPreviewDescription,
                        tags: this.dataArr.convertedTags,
                        previewImageUrl: this.dataArr.previewImageUrl,
                        mainArticleImageUrl: this.dataArr.mainArticleImageUrl,
                        content: this.dataArr.content,
                        markdownTags: this.dataArr.markdownTags,
                        articleId: this.dataArr.articleId,
                        author: this.dataArr.author,
                        authorPhoto: this.dataArr.authorPhoto
                    })
                    this.showPopup = true;
                    this.dataArr.date = '';
                    this.dataArr.apPreviewTitle = '';
                    this.dataArr.apPreviewDescription = '';
                    this.dataArr.tags = '';
                    this.dataArr.convertedTags = '';
                    this.dataArr.previewImageUrl = '';
                    this.dataArr.mainArticleImageUrl = '';
                    this.dataArr.content = '';
                    this.dataArr.markdownTags = '';
                    this.dataArr.author = '';
                } catch (err) {
                    console.log(`Error while sending data to server: ${err}`)
                }

            }
        },
        closePopUp() {
            this.showPopup = false;
        }
    }

});


</script>
