import { createRouter, createWebHashHistory } from "vue-router";
import LoginLayout from './layouts/LoginLayout.vue'
import DashboardLayout from './layouts/DashboardLayout.vue';
import addNew from './layouts/addNew.vue'
import { auth } from './firebaseConfig';
import articleEdit from './layouts/articleEdit.vue'

const requireAuth = (to, from, next) => {
  const isAuthenticated = auth.currentUser;
  if (isAuthenticated) {
    next();
  } else {
    next('/');
  }
};

export default createRouter({
  history: createWebHashHistory(),
  routes: [
    { path: '/', component: LoginLayout },
    { path: '/dashboard', component: DashboardLayout, beforeEnter: requireAuth },
    { path: '/addNew', component: addNew, beforeEnter: requireAuth },
    { path: '/articleEdit', component: articleEdit, beforeEnter: requireAuth },
  ]
});